.App{
  min-height: 10vh;
  
}
.search-form{
  display: flex;
  min-height: 10vh;
  align-items: center;
  justify-content: center;
}

.search-bar
{
  width: 50%;
  border: solid black 2px;
  outline: none;
  border-radius: 20px;
  padding: 10px;
}

.search-btn
{
  border: 2px solid black;
  border-radius: 20px;
  padding :10px 20px;
  margin-left:30px;
}
.content
{
  display:grid;
  grid-template-columns:1fr;
grid-gap:2em;
text-align: center;
justify-content: center;
margin: auto;
}

@media (min-width:600px)
{
  .content
{
  display:grid;
  grid-template-columns: 1fr 1fr;
grid-gap: 2em;
padding: 4em;
text-align: center;
justify-content: center;

}
}