.recipe
{
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 20px;
    background-color:white; 
    box-shadow: 0px 5px 20px black;
    padding: 2em;
    margin:2em;
}
.recipe:hover{
    background-color:rgba(255,255,255,0.6);
    transition-duration: 1s;
    transform: translate(0, -10px);
}